/* -----------------------------
   GLOBAL VARIABLES & BASE SETUP
------------------------------ */
:root {
  --header-total-height: 100px; /* Adjust if Topbar + Navbar total height changes */
  /* --topbar-height: 40px;
  --navbar-height: 60px; */
