/* -----------------------------
   MOBILE DRAWER ANIMATION
------------------------------ */
@keyframes slide-in-right {
  0% {
    transform: translateX(100%);
    opacity: 0;
  }
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}

.animate-slide-in-right {
  animation: slide-in-right 0.28s ease-out forwards;
}

